.sidebyside-text-image {
  position: relative;
  display: grid;
  gap: 2rem;
  padding: 4rem;

  .text {
    border: 1px solid red;
  }

  img {
    border: 1px solid blue;
    width: 100%;
  }

  /* Up until 770px */
  @media (max-width: 770px) {
    grid-template-columns: 1fr;
  }

  /* From 770px on */
  @media (min-width: 770px) {

    &.order-image-text {
      grid-template-columns: 4fr 6fr;
    }

    &.order-text-image {
      grid-template-columns: 6fr 4fr;
    }

  }

}